.card {
    z-index: 1;
}

.footer {
    padding: 10px;
    background-color: #4C598E;

    /* Bottom Stick */
    z-index: 10;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
}

.footerLinks {
    text-align: center;
}

.clickableLink {
    cursor: pointer;
}

.cardBG {
    position: absolute;
    opacity: 0.6;
    z-index: -1;
}

.tooltip { 
    font-size: 0.8rem !important;
    color: #415076 !important;
    border: 1px solid rgba(0,0,0,0.5);
    background-color: #F5F6FA !important;
}